import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import styled from "styled-components";
import { useParams } from "react-router-dom";

const EmailCert = () => {
    const { emailId, authCode } = useParams();

    useEffect(() => {
        const sendEmail = async () => {
            await axios({
                method: "post",
                url: `${process.env.REACT_APP_API_URL}/user/emailCertCheck`,
                data: {
                    userId: emailId,
                    authCode: authCode,
                },
            })
                .then((res) => {
                    alert("인증이 완료되었습니다!");
                })
                .catch((err) => {
                    alert("잘못된 접근입니다. 다시 시도해주세요.");
                    console.log(err);
                });
        };

        sendEmail();
    }, []);
    return <StyledHeading>Email Certification Completed</StyledHeading>;
};

const StyledHeading = styled.p`
    font-size: 24px;
    text-align: center;
    margin-top: 30px;
`;
export default EmailCert;
